.header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
    .name{
      display: flex;
      align-self: center;
    }
    .logo-container {
      height: 100%;
      width: 70px;
      padding: 25px;
    }
  
    .options {
      width: 50%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
  
      .option {
        padding: 10px 15px;
        cursor: pointer;
      }
    }
  }
  
@media(max-width:600){
  .header{
    margin-bottom: 12px;
    height: 40px;
    
  }
}